 Services or any update or upgrade thereto. Merchant is solely responsible for maintaining the Third-Party Services and obtaining any associated licenses and consents necessary for Merchant to use the Third-Party Services in connection with the Services.")]),_c('p',[_vm._v("(d) Tabs Innovation IP and Reservation of Rights. All right, title, and interest in and to (i) the Platform and Services, (ii) the Aggregate Data, (iii) all modifications, improvements, adaptations, enhancements, or translations made to any of the foregoing, and (iv) Tabs Innovation IP and all proprietary rights therein, will be and remain the sole and exclusive property of Tabs Innovation and our licensors. Merchant agrees that the only rights it has with respect to the Platform are the limited access and use rights expressly granted in this Agreement. No rights are granted to Merchant hereunder (whether by implication, estoppel, exhaustion or otherwise) other than as expressly set forth in this Agreement.")]),_c('p',[_vm._v("(e) Feedback. From time-to-time Merchant or its employees, contractors, or representatives may provide Tabs Innovation with suggestions, comments, feedback or the like with regard to the Services (collectively, “Feedback”). Merchant hereby grants Tabs Innovation a worldwide, royalty-free, fully paid-up, perpetual, irrevocable, transferable, sublicensable, exclusive license to use and exploit all Feedback in connection with Tabs Innovation’s business purposes, including, without limitation, the testing, development, maintenance and improvement of the Services.")])]),_c('li',[_c('strong',[_vm._v("PAYMENT PROCESSING AND PROCESSING FEES")]),_c('p',[_vm._v("Tabs Innovation uses a third-party payment processor (the “Processor”) to process all payments made through the Platform. The terms and conditions of the Processor, [https://stripe.com/ssa][the Stripe Connected Account Agreement, which includes the Stripe Services Agreement], are incorporated into this Agreement by reference and by agreeing to the terms of this Agreement, you are also agreement to such the terms and conditions of such Processor agreements. As a condition of Tabs Innovation enabling payment processing services through Processor, Merchant agrees to provide Tabs Innovation accurate and complete information about Merchant’s representative and its business, and Merchant authorizes Tabs Innovation to share such information and all transaction information related to Merchant’s use of the payment processing services provided by Processor. Upon account creation, Merchant shall be solely responsible for the accuracy and integrity of the information associated with their Processor account(s). Tabs Innovation will not be responsible for making additional payments to Merchants due to missing or inaccurate information. Processor has been audited by a PCI-certified auditor and is certified to PCI Service Provider Level 1. Merchant hereby also agrees that Tabs Innovations shall be permitted to charge payment processing fees to Merchant and deduct any such fees due from payments owed to Merchant. At the time of this Agreement such payment processing fee shall be 2.90% of any payments processed through the Platform plus a $0.30 charge per payment transaction/swipe, less a $0.30 credit per Service Fee charged to Customers of Merchant. Such fee and credit amounts and rates may be changed or otherwise modified by Tabs Innovation on three (3) business days notice to Merchant, which notice may be by electronic mail.")])]),_c('li',[_c('strong',[_vm._v("ADDITIONAL MERCHANT OBLIGATIONS.")]),_c('p',[_vm._v("Merchant Obligations Generally. Merchant: (a) hereby authorizes Tabs Innovation to request identity-verifying information about Merchant including a consumer report that contains Merchant’s name and address and authorizes Tabs Innovation to periodically obtain additional reports; and b) shall provide additional information as Tabs Innovation may reasonably request, which information shall be true and correct, for the purposes of meeting onboarding, periodic review requirements, Tabs Innovation’s internal audit, as well as government or regulatory entity requests.")])]),_c('li',[_c('strong',[_vm._v("ADDITIONAL TABS INNOVATION OBLIGATIONS")]),_c('p',[_vm._v("Tabs Innovation shall comply with all applicable domestic and international laws and regulations pertaining to data privacy, personal data, trans-border data flow and data protection (including, without limitation, the requirements of HIPAA and its implementing regulations) and the PCI Data Security Standards (the “Data Protection Requirements”), as applicable, and will implement, keep current and adhere to a set of practices and procedures sufficient to enable such compliance (“Data Protection Requirements”).")])]),_c('li',[_c('strong',[_vm._v("NON-EXCLUSIVITY.")]),_c('p',[_vm._v("Nothing contained in this Agreement shall prohibit Tabs Innovation from offering its services to any third party or using such services or the Tabs Innovation IP in the ordinary course of its business.")])]),_c('li',[_c('strong',[_vm._v("FEES AND PAYMENT, TAXES.")]),_c('ol',[_c('li',[_vm._v("Fees. Tabs Innovation will not charge Merchant any fees at this time, but reserves the right to change its policies and collect from Merchant, in consideration for the Services, non-refundable fees (“Fees”) or applicable charges and to institute new charges and Fees at the end of the initial Subscription Term or any renewal thereof, upon thirty (30) calendar days’ prior notice to Merchant (which may be sent by email). However, Tabs Innovation will charge all users of the Platform a service fee (a “Service Fee”), which will be added to all orders processed through the Platform and shall be deducted from any payments made through the Processor prior to Tabs Innovation transferring any such funds to Merchant. At the time of this Agreement such Service Fee shall be $0.49 per Customer transaction placed through the Platform. Such Service Fee 