ing or deduction. ")]),_c('li',[_vm._v("Third-Party Fees. To the extent that a third party provider provides services to Merchant via the Platform (for example, a middleware provider) and charges fees to Tabs Innovation, Tabs Innovation may pass through such third party’s fees to Merchant. ")]),_c('li',[_vm._v("Required Tax Collection. In some jurisdictions in the United States, Tabs Innovation may be required to collect sales, use, or similar taxes from the Customer and remit such taxes directly to the tax authority. In such jurisdictions, Tabs Innovation will inform Merchant that Tabs Innovation will remit any collected taxes to the tax authority and Tabs Innovation will be relieved of any responsibility to remit such collected taxes to Merchant. ")]),_c('li',[_vm._v("Merchant Review of Charges. Merchant agrees, on an ongoing basis, to review and confirm its transactions, fees and charges on orders and invoices, and to promptly communicate to Tabs Innovation in writing any claimed inaccuracies, so that Tabs Innovation has the prompt opportunity to address and resolve any issues and so such issues do not persist, which both Tabs Innovation and Merchant agree is in the best interests of both parties and their commercial relationship. Merchant agrees to communicate to Tabs Innovation any disagreement, non-conformity or any issue with any transaction, fee, charge or order, including in connection with fraudulent transactions or payments, within fourteen (14) calendar days of payment to Merchant for the transaction, fee or order in question. Merchant shall be deemed to have acquiesced in and ratified, and to have waived any claim or objection regarding, each transaction, fee, charge and order if Merchant does not communicate a written claim or objection to Tabs Innovation regarding such transaction, fee, charge or order within such 14-day period. ")]),_c('li',[_vm._v("Merchant Title to Goods. Merchant agrees that Merchant holds title to the goods or products that Merchant provides through the Platform until the goods are delivered by Merchant to Customer, and that title passes from the Merchant to the Customer upon delivery at the Merchant’s location. Merchant agrees that Tabs Innovation does not hold title to or acquires any ownership interest in any goods or products that Merchant prepares or provides through the Platform. ")])])]),_c('li',[_c('strong',[_vm._v("EQUIPMENT")]),_c('p',[_vm._v(" With respect to the Platform, Merchant will install any equipment reasonably required by Tabs Innovation to access the Platform Services (“Equipment”). Any Equipment provided by Tabs Innovation will remain Tabs Innovation’s sole property and may be used solely for purposes related to fulfilling Merchant’s responsibilities under this Agreement. Merchant will inspect all hardware, and shall notify Tabs Innovation in writing if any Equipment is missing or was damaged as soon as practicable after discovering such damage. Tabs Innovation may restrict or rescind Merchant’s right to use the Platform at any time by giving notice. Merchant will be responsible for any damage to or loss of any Equipment provided by Tabs Innovation (excluding ordinary wear and tear), which will be promptly reimbursed by Merchant (at the replacement cost thereof). Tabs Innovation may recover the replacement cost of damaged or lost Equipment by deducting such amount from any payments to Merchant. Merchant agrees to pay all subscription fees and deposits reasonably charged by Tabs Innovation for Equipment, and agrees Tabs Innovation may deduct such fees and deposits from amounts payable by Tabs Innovation to Merchant. Merchant shall return all Equipment provided by Tabs Innovation to Tabs Innovation within the Return Period. Merchant hereby authorizes Tabs Innovation to put a $250 pre-authorization hold on a credit card provided by Merchant to Tabs Innovation and, in the event Merchant has not returned the Equipment to Tabs Innovation on or before the expiration of the Return Period, Merchant further authorizes Tabs Innovation to charge such $250 amount to such Merchant credit card to offset amounts due to Tabs Innovation in respect of such unreturned Equipment. Tabs Innovation shall be permitted to pursue any and all recourse available to it to recover such Equipment and amounts required to replace such Equipment, including, without limitation, in the event such $250 payment does not cover the full replacement cost of such unreturned Equipment. In the event that Merchant has not returned the Equipment to Tabs Innovation within the Return Period, Merchant shall be responsible for the full cost of such Equipment, which shall be payable to Tabs Innovation within fifteen (15) days of the expiration of the Return Period. Merchant shall not remove, modify, overwrite, obscure or otherwise make not visible any label or other marking on Equipment identifying Equipment as property of Tabs Innovation or another third party. ")])]),_c('li',[_c('strong',[_vm._v("CONFIDENTIAL INFORMATION.")]),_c('p',[_vm._v("(a) The Receiving Party will protect and preserve the Confidential Information of the Disclosing Party as confidential, using no less care than that with which it protects and preserves its own confidential and proprietary information (but in no event less than a reasonable degree of care), and the Receiving Party will maintain the Disclosing Party’s Confidential Information in strict confidence, and will not use the Confidential Information of the Disclosing Party except as necessary to perform its obligations or exercise its rights under this Agreement; provided that Tabs Innovation may use and modify Confidential Information of Merchant in de-identified form for purposes of developing and deriving Aggregate Data. The Receiving Party will not disclose or cause to be disclosed any Confidential Information of the Disclosing Party, except (i) to those employees, representatives, or contractors of the Receiving Party who have a bona fide need 