public body or authority, any lender or lessor, or any other third party, is required to authorize, or is required in connection with, the execution, delivery, and performance of this Agreement by Tabs Innovation. ")])]),_c('li',[_c('strong',[_vm._v("INDEMNIFICATION.")]),_c('p',[_vm._v("(a) Indemnification by Tabs Innovation. Subject to Section 15(b), Tabs Innovation will indemnify, defend and hold Merchant and its Representatives (collectively, the “Merchant Indemnitees”) harmless from and against any and all liabilities, costs, and expenses (including reasonable attorneys’ fees) (“Losses”) incurred by any of the Merchant Indemnitees in connection with any demand, action, claim, or proceeding by a third party (each, a “Claim”) alleging that the use of the Services in accordance with this Agreement, infringes or misappropriates any United States patents, copyrights, or trade secrets.")]),_c('p',[_vm._v("(b) Exclusions. Tabs Innovation’s obligations under Section 15(a) will not apply if the underlying third-party Claim arises from or as a result of: (i) Merchant’s breach of this Agreement, negligence, willful misconduct or fraud; (ii) any Merchant Content; (iii) Merchant’s failure to use any enhancements, modifications, or updates to the Services that have been provided by Tabs Innovation; (iv) modifications to the Services by anyone other than Tabs Innovation; or (v) combinations of the Services with software, data or materials not provided by Tabs Innovation.")]),_c('p',[_vm._v("(c) IP Remedies. If Tabs Innovation reasonably believes the Services (or any component thereof) could infringe any third party’s intellectual property rights, Tabs Innovation may, at its sole option and expense use commercially reasonable efforts to: (i) modify or replace the Services, or any component or part thereof, to make it non-infringing; or (ii) procure the right for Merchant to continue use the Services. If Tabs Innovation determines that neither alternative is commercially practicable, Tabs Innovation may terminate this Agreement and/or the applicable Merchant Application Form, in its entirety or with respect to the affected component, by providing written notice to Merchant. The rights and remedies set forth in this Section 15 will constitute Merchant’s sole and exclusive remedy for any infringement or misappropriation of intellectual property rights in connection with the Services.")]),_c('p',[_vm._v("(d) Indemnification by Merchant. Subject to Section 15(e), Merchant will indemnify, defend and hold Tabs Innovation and its Representatives (collectively, the “Tabs Innovation Indemnitees”) harmless from and against all Losses in connection with any Claim: (i) arising from any Merchant Content, including, without limitation (A) any Claim that the Merchant Content infringes, misappropriates or otherwise violates any third party’s intellectual property rights or privacy or other rights and (B) any Claim that the use, provision, transmission, display or storage of Merchant Content violates any Applicable Law, rule or regulation; (ii) arising from or relating to any products or services of Merchant or Merchant handling and/or processing of orders/transactions through the Platform; (iii) arising from or relating to the use of the Services by Merchant, or any Authorized User in a manner that is not in accordance with this Agreement or the Documentation, including, without limitation, any breach of the restrictions in Section 2(b); and (iv) asserted by any Consumer.")]),_c('p',[_vm._v("(e) Indemnification Procedures. The Party seeking defense and indemnity (the “Indemnified Party”) will promptly (and in any event no later than thirty (30) days after becoming aware of facts or circumstances that could reasonably give rise to any Claim) notify the other Party (the “Indemnifying Party”) of the Claim for which indemnity is being sought, and will reasonably cooperate with the Indemnifying Party in the defense and/or settlement thereof. The Indemnifying Party will have the sole right to conduct the defense of any Claim for which the Indemnifying Party is responsible hereunder (provided; however, that the Indemnifying Party may not settle any Claim without the Indemnified Party’s prior written approval unless the settlement is for a monetary amount, unconditionally releases the Indemnified Party from all liability without prejudice, does not require any admission by the Indemnified Party, and does not place restrictions upon the Indemnified Party’s business, products or services). The Indemnified Party may participate in the defense or settlement of any such Claim at its own expense and with its own choice of counsel or, if the Indemnifying Party refuses to fulfill its obligation of defense, the Indemnified Party may defend itself and seek reimbursement from the Indemnifying Party.")])]),_c('li',[_c('strong',[_vm._v("WARRANTY DISCLAIMER AND LIMITATION OF LIABILITY.")]),_c('ol',[_c('li',[_vm._v(" WARRANTY DISCLAIMER: THE PLATFORM, THE SERVICES, THE MATERIALS, AND ALL OTHER CONTENT ON THE PLATFORM ARE PROVIDED “AS IS” AND “AS AVAILABLE” AND WITHOUT WARRANTIES OF ANY KIND, EITHER EXPRESS OR IMPLIED, UNLESS OTHERWISE SPECIFIED IN WRITING HEREIN. TABS INNOVATION DOES NOT WARRANT OR MAKE ANY REPRESENTATIONS REGARDING THE USE OR THE RESULTS OF THE USE OF THE PLATFORM, THE SERVICES, THE MATERIALS, AND/OR ANY OTHER CONTENT ON THE PLATFORM IN TERMS OF THEIR CORRECTNESS, ACCURACY, RELIABILITY, TIMELINESS, COMPLETENESS, CURRENTNESS, OR OTHERWISE, INCLUDING, WITHOUT LIMITATION, THE SAFETY, QUALITY, AND/OR TIMING OF ITEMS ORDERED ON THE PLATFORM. YOU (AND NOT TABS INNOVATION) ASSUME THE ENTIRE COST OF ALL NECESSARY SERVICING, REPAIR, OR CORRECTION RELATING TO YOUR USE OF THE PLATFORM, THE SERVICES, THE MATERIALS, AND/OR OTHER CONTENT ON THE PLATFORM. TO THE FULLEST EXTENT PERMISSIBLE BY APPLICABLE LAW, TABS INNOVATION DISCLAIMS, WITH RESPECT TO THE SERVICES, THE PLATFORM, THE MATERIALS AND ALL OTHER CONTENT ON THE PLATFORM, ALL WARRANTIES, EXPRESS OR IMPLIED, STATUTORY OR OTHERWISE, INCLUDING, BUT NOT LIMITED TO IMPLIED WARRANTIES OF MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NON-INFRINGEMENT. TABS INNOVATION DOES NOT REPRESENT OR WARRANT THAT THE PLATFORM, THE SERVICES, THE MATERIALS AND/OR THE OTHER CONTENT ON THE PLATFORM WILL BE SECURE, UNINTERRUPTED, AND/OR ERROR-FREE, THAT DEFECTS WILL BE CORRECTED, AND/OR THAT THE PLATFORM, THE SERVICES, THE MATERIALS, AND/OR OTHER CONTENT ON THE PLATFORM ARE FREE FROM VIRUSES OR OTHER HARMFUL COMPONENTS. APPLICABLE LAW MAY NOT ALLOW THE EXCLUSION OF IMPLIED WARRANTIES, SO THE ABOVE EXCLUSION MAY NOT FULLY APPLY TO YOU. ")]),_c('li',[_c('p',[_vm._v("LIMITATION OF LIABILITY: EXCEPT FOR: (I) ANY INFRINGEMENT BY ONE PARTY OF THE OTHER PARTY’S INTELLECTUAL PROPERTY RIGHTS, (II) GROSS NEGLIGENCE, FRAUD OR WILFUL MISCONDUCT BY EITHER PARTY, (III) EITHER PARTY’S INDEMNITY AND DEFENSE OBLIGATIONS UNDER SECTION 15 OR (IV) MERCHANT’S BREACH OF ITS PAYMENT OBLIGATIONS, TO THE FULLEST EXTENT PERMISSIBLE BY APPLICABLE LAW, IN NO EVENT SHALL TABS INNOVATION BE LIABLE TO YOU FOR ANY INDIRECT, SPECIAL, INCIDENTAL, PUNITIVE, EXEMPLARY, OR CONSEQUENTIAL DAMAGES, OR ANY LOSS OR DAMAGES WHATSOEVER (INCLUDING PERSONAL INJURY, LOSS OF DATA, REVENUE, PROFITS, REPUTATION, USE, OR OTHER ECONOMIC ADVANTAGE), EVEN IF TABS INNOVATION HAS BEEN PREVIOUSLY ADVISED OF THE POSSIBILITY OF SUCH DAMAGES, ARISING OUT OF A WARRANTY, CONTRACT, NEGLIGENCE, TORT, OR ANY OTHER ACTION OF ANY TYPE THAT IN ANY MANNER ARISES OUT OF OR IN CONNECTION WITH THE PLATFORM OR THE SERVICES PROVIDED ON OR THROUGH THE PLATFORM OR BY TABS INNOVATION, WHETHER SUCH LIABILITY ARISES FROM ANY CLAIM BASED ON CONTRACT, WARRANTY, TORT (INCLUDING NEGLIGENCE), STRICT LIABILITY OR OTHERWISE, AND WHETHER OR NOT SUCH PARTY WAS ADVISED OF THE POSSIBILITY OF SUCH LOSS OR DAMAGE.")]),_c('p',[_vm._v("TABS INNOVATION SHALL NOT BE LIABLE FOR DELAY OR FAILURE IN PERFORMANCE RESULTING FROM CAUSES BEYOND TABS INNOVATION'S CONTROL, INCLUDING, WITHOUT LIMITATION, DELAYS AND OTHER PROBLEMS INHERENT IN THE USE OF THE INTERNET AND ELECTRONIC COMMUNICATIONS. TABS INNOVATION IS NOT RESPONSIBLE FOR ANY DELAYS, FAILURES, OR OTHER DAMAGE RESULTING FROM SUCH PROBLEMS.")]),_c('p',[_vm._v("TABS INNOVATION ASSUMES NO RESPONSIBILITY OR LIABILITY IN ANY MANNER ARISING OUT OF OR IN CONNECTION WITH ANY INFORMATION, CONTENT, PRODUCTS, SERVICES, OR MATERIAL AVAILABLE ON OR THROUGH THE PLATFORM, AS WELL AS ANY THIRD-PARTY WEBSITE PAGES OR ADDITIONAL WEBSITES LINKED TO THIS PLATFORM, FOR ANY ERROR, DEFAMATION, LIBEL, SLANDER, OMISSION, FALSEHOOD, OBSCENITY, PORNOGRAPHY, PROFANITY, DANGER, INACCURACY CONTAINED THEREIN, OR HARM TO PERSON OR PROPERTY CAUSED THEREBY. THESE LIMITATIONS SHALL APPLY NOTWITHSTANDING ANY FAILURE OF ESSENTIAL PURPOSE OF ANY LIMITED REMEDY. TABS INNOVATION ASSUMES NO RESPONSIBILITY AND SHALL NOT BE LIABLE FOR ANY DAMAGES TO, OR VIRUSES THAT MAY INFECT, YOUR COMPUTER EQUIPMENT OR OTHER PROPERTY ON ACCOUNT OF YOUR ACCESS TO, USE OF, BROWSING OF, OR DOWNLOADING OF ANY MATERIAL FROM THE PLATFORM.")]),_c('p',[_vm._v("IN NO EVENT WILL TABS INNOVATION’S MAXIMUM, CUMULATIVE LIABILITY TO MERCHANT AND THE AUTHORIZED USERS IN CONNECTION WITH THIS AGREEMENT, THE PLATFORM OR THE PROVISION OF THE SERVICES EXCEED THE FEES ACTUALLY PAID BY MERCHANT TO TABS INNOVATION IN THE TWELVE (12) MONTH PERIOD PRECEDING THE EVENT GIVING RISE TO THE FIRST CLAIM, REGARDLESS OF THE LEGAL OR EQUITABLE THEORY ON WHICH THE CLAIM OR LIABILITY IS BASED, AND WHETHER OR NOT TABS INNOVATION WAS ADVISED OF THE POSSIBILITY OF SUCH LOSS OR DAMAGE. EXCEPT FOR MERCHANT’S PAYMENT OBLIGATIONS, IN NO EVENT SHALL MERCHANT’S AGGREGATE LIABILITY TO TABS INNOVATION FOR ANY CLAIMS, LOSSES, INJURIES, SUITS, DEMANDS, JUDGMENTS, LIABILITIES, COSTS, EXPENSES OR DAMAGES FOR ANY CAUSE WHATSOEVER (INCLUDING, BUT NOT LIMITED TO, THOSE ARISING OUT OF OR RELATED TO THIS AGREEMENT OR ANY ORDER) (COLLECTIVELY, “DAMAGES”), AND REGARDLESS OF THE FORM OF ACTION OR LEGAL THEORY, EXCEED THE FEES PAID BY MERCHANT TO TABS INNOVATION UNDER THE APPLICABLE ORDER IN THE TWELVE (12) MONTH PERIOD IMMEDIATELY PRECEDING THE EVENT GIVING RISE TO DAMAGES. THESE LIMITATIONS OF LIABILITY REFLECT THE ALLOCATION OF RISK BETWEEN THE PARTIES.")])]),_c('li',[_vm._v(" BASIS OF THE BARGAIN: THE PARTIES HEREBY ACKNOWLEDGE AND AGREE THAT THE WARRANTY DISCLAIMERS AND LIMITATIONS OF LIABILITY IN THIS SECTION 16 ARE MATERIAL, BARGAINED-FOR BASES OF THIS AGREEMENT, AND THAT THEY HAVE BEEN TAKEN INTO ACCOUNT IN DETERMINING THE CONSIDERATION TO BE GIVEN BY EACH PARTY UNDER THIS AGREEMENT AND IN THE DECISION BY EACH PARTY TO ENTER INTO THIS AGREEMENT. YOU AND TABS INNOVATION AGREE THAT THE WARRANTY DISCLAIMERS AND LIMITATIONS OF LIABILITY IN THIS AGREEMENT ARE FAIR AND REASONABLE. ")])])]),_c('li',[_c('strong',[_vm._v("TERM AND TERMINATION, RENEWAL.")]),_c('p',[_vm._v("(a) Term, Renewal. The term of this Agreement begins on the Effective Date and will continue in effect thereafter at all times until terminated in accordance with Section 17(b). The Subscription Term shall initially be one (1) calendar week (the “Initial Term”) and shall automatically renew prior to the end of the Initial Term for additional successive periods of one (1) calendar week (each a “Renewal Term”) and shall further renew prior to the end of each successive Renewal Terms for successive Renewal Terms, unless such term is terminated early in accordance with the terminations provisions set forth in this Agreement. ")]),_c('p',[_vm._v("(b) Termination. Either Party may terminate this Agreement on written notice to the other Party. In addition, either Party may terminate this Agreement and/or any Merchant Application Form, (i) upon ten (10) calendar days’ prior written notice to the other Party if the other Party commits a material breach of this Agreement, and the breach remains uncured at the expiration of such ten (10) calendar day period; or (ii) immediately, if the other Party (a) is unable to pay its debts as they mature or admits in writing its inability to pay its debts as they mature, (b) makes a general assignment for the benefit of creditors, (c) files a voluntary petition for bankruptcy or has filed against it an involuntary petition for bankruptcy, (d) applies for the appointment of a receiver or trustee for substantially all of its assets or permits the assignment of any such receiver or trustee who is not discharged within a period of thirty (30) calendar days after such appointment, or (e) otherwise ceases to actively conduct its business. We may also terminate this Agreement upon written notice to you under the limited circumstances set forth in Section 15(c). ")]),_c('p',[_vm._v("(c) Survival. Section 2(b) (“Use Restrictions”), Section 2(c) (“Third-Party Services”), Section 2(d) (“Tabs Innovation IP and Reservation of Rights”), Section 2(e) (“Feedback”), Section 3 (“Additional Merchant Obligations”), Section 7 (“Fees and Payment”) (until you have paid all Fees and applicable taxes), Section 8 (“Equipment”) Section 9 (“Confidential Information”), Section 15 (“Indemnification”), Section 16 (“Warranty Disclaimer and Limitations of Liability”), this Section 17(c) (“Survival”), Section 17(d) (“Effect of Termination”), Section 20 (“Dispute Resolution”), Section 21 (“Insurance”), and Section 22 (“General”) shall survive the termination of this Agreement. ")]),_c('p',[_vm._v("(d) Effect of Termination. Upon expiration or termination of this Agreement: (i) the rights granted pursuant to Section 2(a) will terminate; (ii) Merchant will return all equipment provided to Merchant by Tabs Innovation within the applicable Return Period; and (iii) Merchant will return or destroy, at Tabs Innovation’s sole option, all Tabs Innovation Confidential Information in its possession or control, including permanent removal of such Tabs Innovation Confidential Information (consistent with customary industry practice for data destruction) from any storage devices or other hosting environments that are in Merchant’s possession or under Merchant’s control, and at Tabs Innovation’s request, certify in writing to Tabs Innovation that the Tabs Innovation Confidential Information has been returned, destroyed or, in the case of electronic communications, deleted. No expiration or termination will affect Merchant’s obligation to pay all Fees that may have become due or otherwise accrued through the effective date of expiration or termination, or entitle Merchant to any refund. ")])]),_c('li',[_c('strong',[_vm._v("TRADEMARKS.")]),_c('p',[_vm._v("Merchant hereby grants Tabs Innovation a limited, non-exclusive, royalty-free, fully paid-up, transferable (solely to the extent permitted under Section 0(f)) license to use and display Merchant Marks during the Term in connection with (i) the hosting, operation and maintenance of the Services; and (ii) Tabs Innovation’s marketing and promotional efforts for its products and services, including by publicly naming Merchant as a customer of Tabs Innovation and in case studies. All goodwill and improved reputation generated by Tabs Innovation’s use of the Merchant Marks inures to the exclusive benefit of Merchant. Tabs Innovation will use the Merchant Marks in the form stipulated by Merchant and will conform to and observe such standards as Merchant prescribes from time to time in connection with the license granted hereunder (“Marketing Guidelines”). Tabs Innovation may in its sole discretion and subject to any Marketing Guidelines by Merchant at the execution of this Agreement, issue marketing material, press releases or other publicity regarding this Agreement or the contents hereof.")])]),_c('li',[_c('strong',[_vm._v("MARKETING.")]),_c('p',[_vm._v("Merchant agrees that Tabs Innovation may include Merchant’s name and logo in Tabs Innovation’ published Merchant list. ")])]),_c('li',[_c('strong',[_vm._v("DISPUTE RESOLUTION")]),_c('p',[_vm._v("PLEASE READ THIS “DISPUTE RESOLUTION” SECTION CAREFULLY. IT LIMITS THE WAYS YOU CAN SEEK RELIEF FROM TABS INNOVATION AND REQUIRES YOU TO ARBITRATE DISPUTES ON AN INDIVIDUAL BASIS.")]),_c('p',[_vm._v("I. Informal Dispute Resolution Procedure.")]),_c('p',[_vm._v("There might be instances when a dispute arises between you and Tabs Innovation. In those instances, Tabs Innovation is committed to working with you to reach a reasonable resolution; however, we can only do this if we know about and understand each other’s concerns. Therefore, for any issue or dispute that arises between you and Tabs Innovation, both parties acknowledge and agree that they will first make a good faith effort to resolve it informally before initiating any formal dispute resolution proceeding in arbitration or otherwise. This includes first sending a written description of the dispute to the other party. For any dispute you initiate, you agree to send the written description of the dispute along with the email address associated with your account to the following email address: legal@tabs.nyc. For any dispute that Tabs Innovation initiates, we will send our written description of the dispute to the email address associated with your account. The written description must be on an individual basis and provide at least the following information: your name; the nature or basis of the claim or dispute; and the relief sought.")]),_c('p',[_vm._v("You and Tabs Innovation then agree to negotiate in good faith about the dispute through an informal telephonic dispute resolution conference between you and Tabs Innovation. The informal telephonic dispute resolution conferences shall be individualized such that a separate conference must be held each time either party intends to commence individual arbitration; multiple individuals initiating claims cannot participate in the same informal telephonic dispute resolution conference. If either party is represented by counsel, that party's counsel may participate in the informal telephonic dispute resolution conference, but the party also must appear at and participate in the conference. This should lead to resolution, but if for some reason the dispute is not resolved satisfactorily within sixty (60) days after receipt of the written description of the dispute, you and Tabs Innovation agree to the further dispute resolution provisions below.")]),_c('p',[_vm._v("This informal dispute resolution process is a prerequisite and condition precedent to commencing any formal dispute resolution proceeding. The you agree that any relevant limitations period and filing fee or other deadlines will be tolled while the parties engage in this informal dispute resolution process.")]),_c('p',[_vm._v("II. Mutual Arbitration Agreement. You and Tabs Innovation agree that all claims, disputes, or disagreements that may arise out of the interpretation or performance of this Agreement or payments by or to Tabs Innovation, or that in any way relate to your use of the Platform, the Materials, the Services, and/or other content on the Platform, your relationship with Tabs Innovation, or any other dispute with Tabs Innovation, shall be submitted exclusively to binding arbitration. This includes claims that arose, were asserted, or involve facts occurring before the existence of this or any prior Agreement as well as claims that may arise after the termination of this Agreement.")]),_c('p',[_vm._v("Notwithstanding the foregoing, issues related to the scope, validity, and enforceability of this agreement to arbitrate are for a court to decide. Also, each party retains the right to (1) elect to have any claims heard in small claims court on an individual basis for disputes and actions within the scope of such court’s jurisdiction and (2) seek injunctive or other equitable relief in a court of competent jurisdiction to prevent the actual or threatened infringement, misappropriation, or violation of a party's copyrights, trademarks, trade secrets, patents, or other confidential or proprietary information or intellectual property rights.")]),_c('p',[_vm._v("ARBITRATION MEANS THAT AN ARBITRATOR AND NOT A JUDGE OR JURY WILL DECIDE THE CLAIM. RIGHTS TO PREHEARING EXCHANGE OF INFORMATION AND APPEALS MAY BE LIMITED IN ARBITRATION. YOU HEREBY ACKNOWLEDGE AND AGREE THAT YOU AND TABS INNOVATION ARE EACH WAIVING THE RIGHT TO A TRIAL BY JURY TO THE MAXIMUM EXTENT PERMITTED BY LAW.")]),_c('p',[_vm._v("III. Class Action and Collective Relief Waiver.")]),_c('p',[_vm._v("YOU ACKNOWLEDGE AND AGREE THAT, TO THE MAXIMUM EXTENT ALLOWED BY LAW, EXCEPT AS SET OUT IN SECTION VII BELOW, THERE SHALL BE NO RIGHT OR AUTHORITY FOR ANY DISPUTE TO BE ARBITRATED ON A CLASS, JOINT, COLLECTIVE OR CONSOLIDATED BASIS OR IN A PURPORTED REPRESENTATIVE CAPACITY ON BEHALF OF THE GENERAL PUBLIC (SUCH AS CLAIMS AS A PRIVATE ATTORNEY GENERAL OR FOR PUBLIC INJUNCTIVE RELIEF). UNLESS BOTH YOU AND TABS INNOVATION OTHERWISE AGREE IN WRITING, THE ARBITRATOR MAY NOT CONSOLIDATE MORE THAN ONE PERSON’S CLAIMS (EXCEPT AS SET OUT IN SECTION VII BELOW), AND MAY NOT OTHERWISE PRESIDE OVER ANY FORM OF ANY CLASS, JOINT, COLLECTIVE OR REPRESENTATIVE PROCEEDING. THE ARBITRATOR MAY AWARD RELIEF (INCLUDING ANY DECLARATORY OR INJUNCTIVE RELIEF) ONLY IN FAVOR OF THE INDIVIDUAL PARTY SEEKING RELIEF AND ONLY TO THE EXTENT NECESSARY TO RESOLVE AN INDIVIDUAL PARTY’S CLAIM. THE ARBITRATOR MAY NOT AWARD RELIEF FOR OR AGAINST ANYONE WHO IS NOT A PARTY TO THE PROCEEDING.")]),_c('p',[_vm._v("This Class Action and Collective Relief Waiver is an essential part of this “Dispute Resolution” section, and if it is deemed invalid or unenforceable with respect to a particular claim or dispute, neither you nor Tabs Innovation is entitled to arbitration of such claim or dispute. Notwithstanding the foregoing, if a court determines that the Class Action and Collective Relief Waiver is not enforceable as to a particular claim or request for relief and all appeals from that decision have been exhausted (or the decision is otherwise final), then the parties agree that that particular claim or request for relief may proceed in court but shall be severed and stayed pending arbitration of the remaining claims.")]),_c('p',[_vm._v("IV. Arbitration Rules.")]),_c('p',[_vm._v("The arbitration will be administered by the American Arbitration Association (“AAA”). If AAA is not available to arbitrate, the parties will select an alternative arbitration provider. Except as modified by this “Dispute Resolution” provision, the AAA will administer the arbitration in accordance with either (A) the Commercial Arbitration Rules then in effect, or (B) the Consumer Arbitration Rules then in effect if the matter involves a “consumer” agreement as defined by Consumer Arbitration Rule R-1 (together, the “Applicable AAA Rules”). The Applicable AAA Rules are available at https://www.adr.org/Rules or by calling the AAA at 1-800-778-7879.")]),_c('p',[_vm._v("V. Arbitration Process.")]),_c('p',[_vm._v("If after sixty (60) days the Informal Dispute Resolution Procedure above is unsuccessful in resolving the parties’ dispute, a party who desires to initiate arbitration must provide the other party with a written Demand for Arbitration as specified in the Applicable AAA Rules. (The AAA provides applicable forms for Demands for Arbitration available at https://www.adr.org/sites/default/files/Demand_for_Arbitration_0.pdf (Commercial Arbitration Rules) and https://www.adr.org/sites/default/files/Consumer_Demand_for_Arbitration_Form_1.pdf (Consumer Arbitration Rules), and a separate affidavit for waiver of fees for California residents only is available at https://adr.org/sites/default/files/Waiver_of_Fees_CA_Only.pdf.) The arbitrator will be either a retired judge or an attorney licensed to practice law in the state or county in which you reside. The parties will first attempt to agree on an arbitrator. If the parties are unable to agree upon an arbitrator within twenty-one (21) days of receiving the AAA’s list of eligible neutrals, then the AAA will appoint the arbitrator in accordance with the AAA Rules.")]),_c('p',[_vm._v("VI. Arbitration Location and Procedure.")]),_c('p',[_vm._v("Unless you and Tabs Innovation otherwise agree, the arbitration will be conducted in the county where you reside. If the amount in controversy does not exceed $10,000 and you do not seek injunctive or declaratory relief, then the arbitration will be conducted solely on the basis of documents you and Tabs Innovation submit to the arbitrator, unless the arbitrator determines that a hearing is necessary. If the amount in controversy exceeds $10,000 or seeks declaratory or injunctive relief, your right to a hearing will be determined by the Applicable AAA Rules. Subject to the Applicable AAA Rules, the arbitrator will have the discretion to direct a reasonable exchange of information by the parties, consistent with the expedited nature of the arbitration. Unless otherwise prohibited by law, all arbitration proceedings will be confidential and closed to the public and any parties other than you and Tabs Innovation, and all records relating thereto will be permanently sealed, except as necessary to obtain court confirmation of the arbitration award.")]),_c('p',[_vm._v("VII. Batch Arbitration.")]),_c('p',[_vm._v("To increase the efficiency of administration and resolution of arbitrations, in the event 100 or more similar arbitration demands presented by or with the assistance or coordination of the same law firm or organization are submitted to AAA or another arbitration provider (if AAA is unavailable) against Tabs Innovation within a 30-day period (or in otherwise close proximity), the arbitration provider shall (i) administer the arbitration demands in batches of 100 demands per batch (to the extent there are fewer than 100 arbitration demands left over after the batching described above, a final batch will consist of the remaining demands); (ii) designate one arbitrator for each batch; and (iii) provide for a single filing fee due per side per batch. You agree to cooperate in good faith with Tabs Innovation and the arbitration provider to implement such a “batch approach” or other similar approach to provide for an efficient resolution of claims, including the payment of single filing and administrative fees for batches of claims. This “Batch Arbitrations” provision shall in no way be interpreted as authorizing class arbitration of any kind. Tabs Innovation does not agree or consent to class arbitration, private attorney general arbitration, or arbitration involving joint or consolidated claims under any circumstances, except as set forth in this section VII.")]),_c('p',[_vm._v("VIII. Arbitrator's Decision.")]),_c('p',[_vm._v("The arbitrator will render an award within the time frame specified in the applicable AAA Rules. The arbitrator's decision will include the essential findings and conclusions upon which the arbitrator based the award. Judgment on the arbitration award may be entered in any court having jurisdiction thereof. The arbitrator will have the authority to award monetary damages on an individual basis and to grant, on an individual basis, any non-monetary remedy or relief available to an individual to the extent available under applicable law, the arbitral forum’s rules, and this Agreement. The arbitrator's award of damages and/or other relief must be consistent with section III above and also must be consistent with the terms of the “Limitation of Liability” section above as to the types and the amounts of damages or other relief for which a party may be held liable.")]),_c('p',[_vm._v("Attorneys’ fees will be available to the prevailing party in the arbitration only if authorized under applicable substantive law governing the claims in the arbitration. If the arbitrator finds that either the substance of your claim or the relief sought in your Demand for Arbitration was frivolous or was brought for an improper purpose (as measured by the standards set forth in Federal Rule of Civil Procedure 11(b)), Tabs Innovation will have the right to recover attorneys’ fees and expenses.")]),_c('p',[_vm._v("IX. Fees.")]),_c('p',[_vm._v("Your responsibility to pay any AAA filing, administrative, and/or arbitrator fees will be solely as set forth in the applicable AAA Rules (as modified by section VII above). If, however, you are able to demonstrate that the costs of arbitration will be prohibitive for you as compared to the costs of litigation, Tabs Innovation will pay as much of the filing, administration, and/or arbitrator fees as the arbitrator deems necessary to prevent the arbitration from being cost-prohibitive to you.")]),_c('p',[_vm._v("X. Right to Opt-Out of Arbitration.")]),_c('p',[_vm._v("Tabs Innovation’s updates to these Terms and Conditions do not provide you with a new opportunity to opt out of this Mutual Arbitration Agreement if you previously agreed to an Agreement and did not validly opt out of arbitration. Tabs Innovation will continue to honor any valid opt outs if you opted out of this Mutual Arbitration Agreement in a prior version of the Terms and Conditions. To opt out of the Mutual Arbitration Agreement, you must notify Tabs Innovation in writing no later than 30 days after first becoming subject to this Mutual Arbitration Agreement. Your notice must include your name and address, the email address you currently use to access your Tabs Innovation account (if you have one), and a clear statement that you want to opt out of this Mutual Arbitration Agreement. You must send your opt-out notice to: legal@tabs.nyc, with the subject line “ARBITRATION OPT-OUT”. If you opt out of this Mutual Arbitration Agreement, all other parts of this Agreement will continue to apply to you. Opting out of this Mutual Arbitration Agreement has no effect on any other arbitration agreements that you may have entered into with Tabs Innovation or may enter into in the future with Tabs Innovation.")]),_c('p',[_vm._v("XI. Changes.")]),_c('p',[_vm._v("Tabs Innovation reserves the right to change this “Dispute Resolution” section, but any such changes will not apply to any individual claim(s) for which you have already provided notice to Tabs Innovation. If Tabs Innovation changes this “Dispute Resolution” section after the date you first accepted this Agreement (or accepted any subsequent changes to this Agreement), you agree that your continued use of the Platform or Services after such change will be deemed acceptance of those changes. If you do not agree to such change, you may reject any such change by providing Tabs Innovation written notice of such rejection by mail or hand delivery to: Tabs Innovation, Attn: Dispute Resolutions, 500 7th Avenue, 8th Floor, New York, NY 10018, or by email from the email address associated with your account to: legal@tabs.nyc, within 30 calendar days of the date such change became effective, as indicated in the “Effective Date” above. In order to be effective, the notice must include your full name and clearly indicate your intent to reject changes to this “Dispute Resolution” section. By rejecting changes, you are agreeing that you will arbitrate any dispute between you and Tabs Innovation in accordance with the provisions of this “Dispute Resolution” section as of the date you first accepted this Agreement (or accepted any subsequent changes to this Agreement, as applicable).")])]),_c('li',[_c('strong',[_vm._v(" INSURANCE")]),_c('p',[_vm._v(" During the term of the Agreement and for one year after, each party hereto shall maintain adequate insurance in amounts not less than as required by law or that is common practice in such party’s business, which shall include, without limitation, general business liability insurance. Upon request, each party will provide the other with current evidence of coverage. Such insurance shall not be cancelled or materially reduced without thirty (30) days’ prior written notice. In no event shall the limits of any insurance policy be considered as limiting the liability of a party under this Agreement. ")])]),_c('li',[_c('strong',[_vm._v("GENERAL.")]),_c('ol',[_c('li',[_vm._v("Entire Agreement. This Agreement, including its exhibits and any Merchant Application Form, is the complete and exclusive agreement between the Parties with respect to its subject matter and supersedes any and all prior or contemporaneous agreements, communications and understandings, both written and oral, with respect to its subject matter. This Agreement may be amended or modified only by a written document executed by duly authorized representatives of the Parties. ")]),_c('li',[_vm._v("Notices. All notices required or permitted under this Agreement will be in writing, will reference this Agreement, and will be sent to the relevant address set forth below or to such other address as may be specified by the relevant Party to the other Party in accordance with this Section 0 (b). Such notices will be deemed given: (i) when delivered personally; (ii) one (1) business day after deposit with a nationally recognized express courier, with written confirmation of receipt; (iii) three (3) business days after having been sent by registered or certified mail, return receipt requested, postage prepaid; or (iv) when sent by email, on the date the email was sent without a bounce back message if sent during normal business hours of the receiving party, and on the next business day if sent after normal business hours of the receiving party. "),_c('p',[_vm._v("If to Tabs Innovation:")]),_c('code',[_vm._v(" Tabs Innovation 500 7th Avenue, 8th Floor New York, NY 10018 Attention: Lucas Kooyman Email: legal@tabs.nyc ")]),_c('p',[_vm._v("If to Merchant:")]),_c('code',[_vm._v(" To the address provided in the Merchant Application Form. ")])]),_c('li',[_vm._v("Waiver. Either Party’s failure to enforce any provision of this Agreement will not constitute a waiver of future enforcement of that or any other provision. No waiv