          <strong>DISPUTE RESOLUTION</strong>
          <p>PLEASE READ THIS “DISPUTE RESOLUTION” SECTION CAREFULLY. IT LIMITS THE WAYS YOU CAN
            SEEK RELIEF FROM TABS INNOVATION AND REQUIRES YOU TO ARBITRATE DISPUTES ON AN INDIVIDUAL
            BASIS.</p>

          <p>I. Informal Dispute Resolution Procedure.</p>
          <p>There might be instances when a dispute arises between you and Tabs Innovation. In
            those instances, Tabs Innovation is committed to working with you to reach a reasonable
            resolution; however, we can only do this if we know about and understand each other’s
            concerns. Therefore, for any issue or dispute that arises between you and Tabs
            Innovation, both parties acknowledge and agree that they will first make a good faith
            effort to resolve it informally before initiating any formal dispute resolution
            proceeding in arbitration or otherwise. This includes first sending a written
            description of the dispute to the other party. For any dispute you initiate, you agree
            to send the written description of the dispute along with the email address associated
            with your account to the following email address: legal@tabs.nyc. For any dispute that
            Tabs Innovation initiates, we will send our written description of the dispute to the
            email address associated with your account. The written description must be on an
            individual basis and provide at least the following information: your name; the nature
            or basis of the claim or dispute; and the relief sought.</p>
          <p>You and Tabs Innovation then agree to negotiate in good faith about the dispute through
            an informal telephonic dispute resolution conference between you and Tabs Innovation.
            The informal telephonic dispute resolution conferences shall be individualized such that
            a separate conference must be held each time either party intends to commence individual
            arbitration; multiple individuals initiating claims cannot participate in the same
            informal telephonic dispute resolution conference. If either party is represented by
            counsel, that party's counsel may participate in the informal telephonic dispute
            resolution conference, but the party also must appear at and participate in the
            conference. This should lead to resolution, but if for some reason the dispute is not
            resolved satisfactorily within sixty (60) days after receipt of the written description
            of the dispute, you and Tabs Innovation agree to the further dispute resolution
            provisions below.</p>
          <p>This informal dispute resolution process is a prerequisite and condition precedent to
            commencing any formal dispute resolution proceeding. The you agree that any relevant
            limitations period and filing fee or other deadlines will be tolled while the parties
            engage in this informal dispute resolution process.</p>
          <p>II. Mutual Arbitration Agreement. You and Tabs Innovation agree that all claims,
            disputes, or disagreements that may arise out of the interpretation or performance of
            this Agreement or payments by or to Tabs Innovation, or that in any way relate to your
            use of the Platform, the Materials, the Services, and/or other content on the Platform,
            your relationship with Tabs Innovation, or any other dispute with Tabs Innovation, shall
            be submitted exclusively to binding arbitration. This includes claims that arose, were
            asserted, or involve facts occurring before the existence of this or any prior Agreement
            as well as claims that may arise after the termination of this Agreement.</p>
          <p>Notwithstanding the foregoing, issues related to the scope, validity, and
            enforceability of this agreement to arbitrate are for a court to decide. Also, each
            party retains the right to (1) elect to have any claims heard in small claims court on
            an individual basis for disputes and actions within the scope of such court’s
            jurisdiction and (2) seek injunctive or other equitable relief in a court of competent
            jurisdiction to prevent the actual or threatened infringement, misappropriation, or
            violation of a party's copyrights, trademarks, trade secrets, patents, or other
            confidential or proprietary information or intellectual property rights.</p>
          <p>ARBITRATION MEANS THAT AN ARBITRATOR AND NOT A JUDGE OR JURY WILL DECIDE THE CLAIM.
            RIGHTS TO PREHEARING EXCHANGE OF INFORMATION AND APPEALS MAY BE LIMITED IN ARBITRATION.
            YOU HEREBY ACKNOWLEDGE AND AGREE THAT YOU AND TABS INNOVATION ARE EACH WAIVING THE RIGHT
            TO A TRIAL BY JURY TO THE MAXIMUM EXTENT PERMITTED BY LAW.</p>
          <p>III. Class Action and Collective Relief Waiver.</p>
          <p>YOU ACKNOWLEDGE AND AGREE THAT, TO THE MAXIMUM EXTENT ALLOWED BY LAW, EXCEPT AS SET OUT
            IN SECTION VII BELOW, THERE SHALL BE NO RIGHT OR AUTHORITY FOR ANY DISPUTE TO BE
            ARBITRATED ON A CLASS, JOINT, COLLECTIVE OR CONSOLIDATED BASIS OR IN A PURPORTED
            REPRESENTATIVE CAPACITY ON BEHALF OF THE GENERAL PUBLIC (SUCH AS CLAIMS AS A PRIVATE
            ATTORNEY GENERAL OR FOR PUBLIC INJUNCTIVE RELIEF). UNLESS BOTH YOU AND TABS INNOVATION
            OTHERWISE AGREE IN WRITING, THE ARBITRATOR MAY NOT CONSOLIDATE MORE THAN ONE PERSON’S
            CLAIMS (EXCEPT AS SET OUT IN SECTION VII BELOW), AND MAY NOT OTHERWISE PRESIDE OVER ANY
            FORM OF ANY CLASS, JOINT, COLLECTIVE OR REPRESENTATIVE PROCEEDING. THE ARBITRATOR MAY
            AWARD RELIEF (INCLUDING ANY DECLARATORY OR INJUNCTIVE RELIEF) ONLY IN FAVOR OF THE
            INDIVIDUAL PARTY SEEKING RELIEF AND ONLY TO THE EXTENT NECESSARY TO RESOLVE AN
            INDIVIDUAL PARTY’S CLAIM. THE ARBITRATOR MAY NOT AWARD RELIEF FOR OR AGAINST ANYONE WHO
            IS NOT A PARTY TO THE PROCEEDING.</p>
          <p>This Class Action and Collective Relief Waiver is an essential part of this “Dispute
            Resolution” section, and if it is deemed invalid or unenforceable with respect to a
            particular claim or dispute, neither you nor Tabs Innovation is entitled to arbitration
            of such claim or dispute. Notwithstanding the foregoing, if a court determines that the
            Class Action and Collective Relief Waiver is not enforceable as to a particular claim or
            request for relief and all appeals from that decision have been exhausted (or the
            decision is otherwise final), then the parties agree that that particular claim or
            request for relief may proceed in court but shall be severed and stayed pending
            arbitration of the remaining claims.</p>
          <p>IV. Arbitration Rules.</p>
          <p>The arbitration will be administered by the American Arbitration Association (“AAA”).
            If AAA is not available to arbitrate, the parties will select an alternative arbitration
            provider. Except as modified by this “Dispute Resolution” provision, the AAA will
            administer the arbitration in accordance with either (A) the Commercial Arbitration
            Rules then in effect, or (B) the Consumer Arbitration Rules then in effect if the matter
            involves a “consumer” agreement as defined by Consumer Arbitration Rule R-1 (together,
            the “Applicable AAA Rules”). The Applicable AAA Rules are available at
            https://www.adr.org/Rules or by calling the AAA at 1-800-778-7879.</p>
          <p>V. Arbitration Process.</p>
          <p>If after sixty (60) days the Informal Dispute Resolution Procedure above is
            unsuccessful in resolving the parties’ dispute, a party who desires to initiate
            arbitration must provide the other party with a written Demand for Arbitration as
            specified in the Applicable AAA Rules. (The AAA provides applicable forms for Demands
            for Arbitration available at
            https://www.adr.org/sites/default/files/Demand_for_Arbitration_0.pdf (Commercial
            Arbitration Rules) and
            https://www.adr.org/sites/default/files/Consumer_Demand_for_Arbitration_Form_1.pdf
            (Consumer Arbitration Rules), and a separate affidavit for waiver of fees for California
            residents only is available at
            https://adr.org/sites/default/files/Waiver_of_Fees_CA_Only.pdf.) The arbitrator will be
            either a retired judge or an attorney licensed to practice law in the state or county in
            which you reside. The parties will first attempt to agree on an arbitrator. If the
            parties are unable to agree upon an arbitrator within twenty-one (21) days of receiving
            the AAA’s list of eligible neutrals, then the AAA will appoint the arbitrator in
            accordance with the AAA Rules.</p>
          <p>VI. Arbitration Location and Procedure.</p>
          <p>Unless you and Tabs Innovation otherwise agree, the arbitration will be conducted in
            the county where you reside. If the amount in controversy does not exceed $10,000 and
            you do not seek injunctive or declaratory relief, then the arbitration will be conducted
            solely on the basis of documents you and Tabs Innovation submit to the arbitrator,
            unless the arbitrator determines that a hearing is necessary. If the amount in
            controversy exceeds $10,000 or seeks declaratory or injunctive relief, your right to a
            hearing will be determined by the Applicable AAA Rules. Subject to the Applicable AAA
            Rules, the arbitrator will have the discretion to direct a reasonable exchange of
            information by the parties, consistent with the expedited nature of the arbitration.
            Unless otherwise prohibited by law, all arbitration proceedings will be confidential and
            closed to the public and any parties other than you and Tabs Innovation, and all records
            relating thereto will be permanently sealed, except as necessary to obtain court
            confirmation of the arbitration award.</p>
          <p>VII. Batch Arbitration.</p>
          <p>To increase the efficiency of administration and resolution of arbitrations, in the
            event 100 or more similar arbitration demands presented by or with the assistance or
            coordination of the same law firm or organization are submitted to AAA or another
            arbitration provider (if AAA is unavailable) against Tabs Innovation within a 30-day
            period (or in otherwise close proximity), the arbitration provider shall (i) administer
            the arbitration demands in batches of 100 demands per batch (to the extent there are
            fewer than 100 arbitration demands left over after the batching described above, a final
            batch will consist of the remaining demands); (ii) designate one arbitrator for each
            batch; and (iii) provide for a single filing fee due per side per batch. You agree to
            cooperate in good faith with Tabs Innovation and the arbitration provider to implement
            such a “batch approach” or other similar approach to provide for an efficient resolution
            of claims, including the payment of single filing and administrative fees for batches of
            claims. This “Batch Arbitrations” provision shall in no way be interpreted as
            authorizing class arbitration of any kind. Tabs Innovation does not agree or consent to
            class arbitration, private attorney general arbitration, or arbitration involving joint
            or consolidated claims under any circumstances, except as set forth in this section
            VII.</p>
          <p>VIII. Arbitrator's Decision.</p>
          <p>The arbitrator will render an award within the time frame specified in the applicable
            AAA Rules. The arbitrator's decision will include the essential findings and conclusions
            upon which the arbitrator based the award. Judgment on the arbitration award may be
            entered in any court having jurisdiction thereof. The arbitrator will have the authority
            to award monetary damages on an individual basis and to grant, on an individual basis,
            any non-monetary remedy or relief available to an individual to the extent available
            under applicable law, the arbitral forum’s rules, and this Agreement. The arbitrator's
            award of damages and/or other relief must be consistent with section III above and also
            must be consistent with the terms of the “Limitation of Liability” section above as to
            the types and the amounts of damages or other relief for which a party may be held
            liable.</p>
          <p>Attorneys’ fees will be available to the prevailing party in the arbitration only if
            authorized under applicable substantive law governing the claims in the arbitration. If
            the arbitrator finds that either the substance of your claim or the relief sought in
            your Demand for Arbitration was frivolous or was brought for an improper purpose (as
            measured by the standards set forth in Federal Rule of Civil Procedure 11(b)), Tabs
            Innovation will have the right to recover attorneys’ fees and expenses.</p>
          <p>IX. Fees.</p>
          <p>Your responsibility to pay any AAA filing, administrative, and/or arbitrator fees will
            be solely as set forth in the applicable AAA Rules (as modified by section VII above).
